import React, {Component} from 'react';
// import ReactPlayer from 'react-player'
import ChatBot from 'react-simple-chatbot';
import PropTypes from 'prop-types';
import $ from 'jquery';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            opened: true,
            loading: true,
            trigger: false,
            steps: [{id: 1, message: 'hello', trigger: 2},
                    {id: 2, user: true, end: true}]
        };
        this.toggleFloating = this.toggleFloating.bind(this);
        this.fetchData = this.fetchData.bind(this);
    }

    fetchData(data) {
        this.setState({steps: data.steps}); // Notice this
    }

    componentWillMount() {
        setTimeout(() => this.setState({ loading: false }), 1500);
        const self = this;
        var json = {id: 'user123456789', message: 'Ernährung', end: "True"};
        $.ajax({
            url: `https://zenzai.synapse.boot.ai/api/v1`,
            type: "POST",
            dataType: 'json',
            data: JSON.stringify(json),
            cache: false,
            success: function(steps) {
                self.fetchData(steps);
            }.bind(this),
            error: function(xhr, status, err) {
                console.error(this.props.url, status, err.toString());
            }.bind(this)
        });
    }

    render() {
        const {opened, steps, loading } = this.state;
        if (loading) {
            return null;
        }
        return (
        <ChatBot
            handleEnd={this.handleEnd}
            steps={steps}
            floating={true}
            opened={opened}
            toggleFloating={this.toggleFloating}
        />
        );
    }

    handleEnd({steps, values}) {

    }

    toggleFloating({opened}) {
        this.setState({opened});
    }
}

App.propTypes = {
    steps: PropTypes.object,
};

App.defaultProps = {
    steps: undefined,
};

export default App;



